.controls-wrapper {
  display: flex;
  justify-content: start;
  gap: 5px;
  margin-bottom: 70px;

  position: relative;
}

.car-wrapper {
  padding-bottom: -20px;
  margin: 20px 0;
  position: relative;
}

.car-wrapper:after {
  content: '';
  position: absolute;
  bottom: -48px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: green;
}

.car {
  position: absolute; 
  left: 0;
  bottom: -60px;
}

.flag {
  position: absolute; 
  right: 130px;
  bottom: -60px;

  width: 80px;
  height: 80px;

  background-image: url(../../../public/2391367.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

