.page_num-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.page_num {
  padding: 5px 10px;
  color: green;
  border: 2px solid green;
  background: transparent;
  cursor: pointer;
}

.page_num--current {
  background-color: green;
  color: #fff;
}

.winner-text {
  position: absolute;
  color: goldenrod;

  top: 50%;
  left: 50%;
  transform: translate(-50%);
}